import { IStore } from '../stores/IStore';
import { combineReducers } from 'redux';
import { loadedWelfareCentres } from './loadedWelfareCentres';
import { filteredWelfareCentres } from './filteredWelfareCentres';
import { selectedWelfareCentre } from './selectedWelfareCentre';
import { isSearchConfirmed } from './isSearchConfirmed';
import { windowSize } from './windowSize';

export const rootReducer = combineReducers<IStore>({
  filteredWelfareCentres,
  isSearchConfirmed,
  loadedWelfareCentres,
  selectedWelfareCentre,
  windowSize,
});
