import { Action } from '../@types/Dispatcher';
import { App_Window_Resized } from '../constants/actionTypes';
import { WindowSize } from '../@types/windowSize';

const initialValue: WindowSize = { width: window.innerWidth, height: window.innerWidth };

export function windowSize(
  state = initialValue,
  action: Action,
): WindowSize {
  switch (action.type) {
    case App_Window_Resized:
      return action.payload;
    default:
      return state;
  }
}
