import React, { Component } from 'react';
import { Map } from '../containers/Map';
import './App.css';
import { GoogleMapsUrl } from '../constants/mapConfig';
import { SideBar } from '../containers/SideBar';
import { debounce } from '../utils/general/debounce';
import { WindowSize } from '../@types/windowSize';

export interface IAppDispatchProps {
  readonly onInit: () => Promise<void>;
  readonly onWindowResized: (size: WindowSize) => void;
}

type AppProps = IAppDispatchProps;

export class App extends Component<AppProps> {
  private readonly _debouncedWindowResize = null;

  constructor(props: AppProps) {
    super(props);
    this._debouncedWindowResize = debounce(() => {
      props.onWindowResized({ width: window.innerWidth, height: window.innerHeight });
    }, 200);
  }

  componentDidMount(): void {
    this.props.onInit();
    window.addEventListener('resize', this._debouncedWindowResize);
  }

  componentWillUnmount(): void {
    window.removeEventListener('resize', this._debouncedWindowResize);
  }

  render(): JSX.Element {
    return (
      <div className="app">
        <SideBar />
        <Map
          googleMapURL={GoogleMapsUrl}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `100%` }} />}
          mapElement={<div style={{ height: `100%` }} />}
        />
      </div>
    );
  }
}
