import React, { ChangeEvent, Component, FormEvent, PureComponent } from 'react';
import { debounce, DebouncedFunction } from '../utils/general/debounce';
import { MaxSearchPatternLength } from '../constants/config';
import './FullTextSearch.css';
import icons from '../assets/icons.svg';
import { renderSvgIcon } from '../utils/renderUtils';

interface IFullTextSearchStateProps {
  readonly searchPhrase?: string;
  readonly showBackButton: boolean;
}

interface IFullTextSearchCallbackProps {
  readonly onBackButtonClicked: () => void;
  readonly onSearchConfirmed: () => void;
  readonly onSearchPhraseChanged: (searchPhrase: string) => void;
}

type FullTextSearchProps =
  IFullTextSearchStateProps &
  IFullTextSearchCallbackProps;

export class FullTextSearch extends PureComponent<FullTextSearchProps> {
  static displayName = 'FullTextSearch';

  private _input: HTMLInputElement = null;

  private _onChange(value: string): void {
    this.props.onSearchPhraseChanged(value);
  }

  private _onInputChange = (e: ChangeEvent<HTMLInputElement>): void => {
    this._onChange(e.target.value);
  };

  private _onClearButtonClicked = (): void => {
    this._onChange('');
    this.focus();
  };

  private _onFormSubmit = (event: FormEvent<HTMLFormElement>): void => {
    this.props.onSearchConfirmed();
    event.preventDefault();
  };

  public focus() {
    this._input.focus();
  }

  render(): JSX.Element {
    const {
      searchPhrase,
      showBackButton,
      onBackButtonClicked,
    } = this.props;

    return (
      <div className="fulltext">
        <form
          onSubmit={this._onFormSubmit}
        >
          <div className="fulltext__inputs">
            {showBackButton && (
              <label className="fulltext__button-back">
                {renderSvgIcon(icons + '#back')}
                <input
                  type="button"
                  onClick={onBackButtonClicked}
                />
              </label>
            )}
            <input
              className="fulltext__input-text"
              maxLength={MaxSearchPatternLength}
              onChange={this._onInputChange}
              placeholder="Hledat..."
              ref={c => this._input = c}
              tabIndex={1}
              type="text"
              value={searchPhrase || ''}
            />
            <label className="fulltext__button-search" tabIndex={2}>
              {renderSvgIcon(icons + '#magnifying-glass')}
              <input
                type="submit"
              />
            </label>
            <label className="fulltext__button-clear" tabIndex={3}>
              {renderSvgIcon(icons + '#close')}
              <input
                onClick={this._onClearButtonClicked}
                type="button"
              />
            </label>
          </div>
        </form>
      </div>
    );
  }
}
