import { WelfareCentre } from '../models/WelfareCentre';
import { App_Init_Finished } from '../constants/actionTypes';
import { Action } from '../@types/Dispatcher';
import { initialState } from '../stores/initialState';

export function loadedWelfareCentres(
  state = initialState.loadedWelfareCentres,
  action: Action,
): Array<WelfareCentre> {
  switch (action.type) {
    case App_Init_Finished:
      return action.payload.loadedWelfareCentres;
    default:
      return state;
  }
}
