import React, { Component, PureComponent } from 'react';
import { FullTextSearch } from './FullTextSearch';
import { CategorySearch } from './CategorySearch';
import { getCategoryNameForKey } from '../utils/categoryUtils';

export interface ISearchStateProps {
  readonly selectedWelfareCentreName?: string;
  readonly showCategorySearch: boolean;
  readonly showBackButton: boolean;
}

export interface ISearchState {
  readonly searchPhrase?: string;
}

export interface ISearchDispatchProps {
  readonly onBack: () => void;
  readonly onFocus: () => void;
  readonly onSearch: (searchPhrase: string) => void;
}

type SearchProps =
  ISearchStateProps &
  ISearchDispatchProps;

export class Search extends PureComponent<SearchProps, ISearchState> {
  static displayName = 'Search';

  private _fullTextComponent: FullTextSearch = null;

  constructor(props) {
    super(props);
    this.state = {};
  }

  private _onCategorySelected = (key: string) => {
    this.setState(() => ({
      searchPhrase: getCategoryNameForKey(key),
    }), () => {
      this._onSearchConfirmed();
    });
  };

  private _onSearchPhraseChanged = (searchPhrase: string) => {
    this.setState(() => ({
      searchPhrase,
    }));
  };

  private _onSearchConfirmed = (): void => {
    this.props.onSearch(this.state.searchPhrase);
  }

  render(): JSX.Element {
    const {
      showBackButton,
      showCategorySearch,
      onBack,
    } = this.props;
    const {
      searchPhrase,
    } = this.state;

    return (
      <div
        className="search"
        onFocus={this.props.onFocus}
      >
        <FullTextSearch
          ref={c => this._fullTextComponent = c}
          searchPhrase={searchPhrase}
          showBackButton={showBackButton}
          onBackButtonClicked={onBack}
          onSearchConfirmed={this._onSearchConfirmed}
          onSearchPhraseChanged={this._onSearchPhraseChanged}
        />
        {showCategorySearch && (
          <CategorySearch
            onCategorySelected={this._onCategorySelected}
          />
        )}
      </div>
    );
  }
}
