import { ContentItem, Fields } from 'kentico-cloud-delivery';

export class WelfareCentre extends ContentItem {
  public name: Fields.TextField;
  public serviceCategories: Fields.MultipleChoiceField;
  public serviceDescription: Fields.RichTextField;
  public openingHours: Fields.RichTextField;
  public street: Fields.TextField;
  public city: Fields.TextField;
  public zip: Fields.TextField;
  public gpsLatitude: Fields.NumberField;
  public gpsLongitude: Fields.NumberField
  public phone: Fields.TextField;
  public web: Fields.TextField;

  constructor(){
    super({
      propertyResolver: ((fieldName: string) => {
        if (fieldName === "service_categories"){
          return "serviceCategories";
        }
        if (fieldName === "service_description"){
          return "serviceDescription";
        }
        if (fieldName === "opening_hours"){
          return "openingHours";
        }
        if (fieldName === "gps_latitude"){
          return "gpsLatitude";
        }
        if (fieldName === "gps_longitude"){
          return "gpsLongitude";
        }
        return fieldName;
      })
    })
  }
}
