import { Dispatch } from '../@types/Dispatcher';
import { ComponentClass } from 'react';
import { connect } from 'react-redux';
import {
  ISearchDispatchProps, ISearchStateProps,
  Search as SearchComponent,
} from '../components/Search';
import { searchConfirmed } from '../actions/thunkActions';
import { IStore } from '../stores/IStore';
import { MobileBreakpoint } from '../constants/config';
import { goBack, searchFocused } from '../actions/actions';

export interface ISearchContainerProps {
}

const mapStateToProps = (state: IStore): ISearchStateProps => {
  const isMobileView = state.windowSize.width < MobileBreakpoint;
  const isConfirmedSearchInMobileView = isMobileView && state.isSearchConfirmed;
  const showCategorySearch = !isConfirmedSearchInMobileView && !state.selectedWelfareCentre;
  const showBackButton = !!state.selectedWelfareCentre;

  return {
    showBackButton,
    showCategorySearch,
    selectedWelfareCentreName: state.selectedWelfareCentre ? state.selectedWelfareCentre.name.value : undefined,
  };
};

const mapDispatchToProps = (dispatch: Dispatch): ISearchDispatchProps => ({
  onBack: () => dispatch(goBack()),
  onFocus: () => dispatch(searchFocused()),
  onSearch: (searchPhrase) => dispatch(searchConfirmed(searchPhrase)),
});

export const Search: ComponentClass<ISearchContainerProps> = connect(
  mapStateToProps,
  mapDispatchToProps,
)(SearchComponent);
