import React, { PureComponent } from 'react';
import { GoogleMap } from 'react-google-maps';
import MarkerClusterer from 'react-google-maps/lib/components/addons/MarkerClusterer';
import {
  ClustererGridSize,
  DefaultCenter,
  ZoomForCurrentLocation,
  DefaultZoom,
  MarkerLabelsZoomThreshold
} from '../constants/mapConfig';
import { WelfareCentre } from '../models/WelfareCentre';
import { CustomMarker } from './CustomMarker';
import './Map.css';
import { getWelfareCentresWithinBounds } from '../utils/searchUtils';

export interface IMapStateProps {
  readonly isMobileView: boolean;
  readonly welfareCentres: Array<WelfareCentre>;
}

export interface IMapDispatchProps {
  readonly onMapFocused: () => void;
  readonly onWelfareCentreSelected: (id: string) => Promise<void>;
}

export interface IMapState {
  readonly currentLocation?: LatLng;
  readonly currentZoom: number;
}

type MapProps =
  IMapStateProps &
  IMapDispatchProps;

export class Map extends PureComponent<MapProps, IMapState> {
  static displayName = 'Map';

  private _mapComponent: GoogleMap = null;

  constructor(props) {
    super(props);
    this.state = {
      currentLocation: DefaultCenter,
      currentZoom: DefaultZoom,
    };
  }

  private _onMarkerClick = (id: string, latLng: google.maps.LatLng): void => {
    this.props.onWelfareCentreSelected(id);
    this._mapComponent.panTo(latLng);
  };

  private _onZoomChanged = (): void => {
    this.setState(() => ({
      currentZoom: this._mapComponent.getZoom(),
    }));
  };

  private _tryGetCurrentLocation(): void {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        position => {
          this.setState(() => ({
            currentLocation: { lat: position.coords.latitude, lng: position.coords.longitude },
            currentZoom: ZoomForCurrentLocation,
          }));
        }
      );
    }
  }

  private _fitBoundsToVisibleMarkers = (welfareCentres: Array<WelfareCentre>): void => {
    const bounds = new google.maps.LatLngBounds();
    welfareCentres.forEach((centre: WelfareCentre) => {
      const loc = new google.maps.LatLng(centre.gpsLatitude.value, centre.gpsLongitude.value);
      bounds.extend(loc);
    })
    this._mapComponent.fitBounds(bounds);
  };

  componentDidMount(): void {
    this._tryGetCurrentLocation();
  }

  componentDidUpdate(prevProps: IMapStateProps): void {
    const welfareCentres = this.props.welfareCentres;
    if (welfareCentres !== prevProps.welfareCentres && welfareCentres.length > 0) {
      const centresInCurrentLocation =
        this.state.currentLocation &&
        getWelfareCentresWithinBounds(welfareCentres, this._mapComponent.getBounds());

      const centresToZoom = centresInCurrentLocation.length > 1 ? centresInCurrentLocation : welfareCentres;
      this._fitBoundsToVisibleMarkers(centresToZoom);
    }
  }

  render(): JSX.Element {
    const {
      isMobileView,
      onMapFocused,
      welfareCentres,
    } = this.props;
    const {
      currentZoom,
      currentLocation,
    } = this.state;

    return (
      <GoogleMap
        center={currentLocation}
        defaultCenter={DefaultCenter}
        defaultZoom={DefaultZoom}
        onZoomChanged={this._onZoomChanged}
        onClick={onMapFocused}
        options={{
          fullscreenControl: false,
          mapTypeControlOptions: {
            position: isMobileView ? google.maps.ControlPosition.BOTTOM_RIGHT : google.maps.ControlPosition.TOP_RIGHT,
          },
          maxZoom: 18,
        }}
        ref={c => this._mapComponent = c}
        zoom={currentZoom}
      >
        {welfareCentres && (
          <MarkerClusterer
            averageCenter
            enableRetinaIcons
            gridSize={ClustererGridSize}
          >
            {welfareCentres.map((centre: WelfareCentre) =>
              <CustomMarker
                id={centre.system.codename}
                key={centre.system.codename}
                latLng={{lat: centre.gpsLatitude.value, lng: centre.gpsLongitude.value}}
                name={centre.name.value}
                onClick={this._onMarkerClick}
                showLabels={currentZoom > MarkerLabelsZoomThreshold}
              />
            )}
          </MarkerClusterer>
        )}
      </GoogleMap>
    );
  }
}
