import {IStore} from '../stores/IStore';
import {
  IMapDispatchProps,
  IMapStateProps,
  Map as MapComponent
} from '../components/Map';
import { ComponentClass } from 'react';
import { connect } from 'react-redux';
import { withGoogleMap, withScriptjs } from 'react-google-maps';
import { WithGoogleMapProps } from 'react-google-maps/lib/withGoogleMap';
import { WithScriptjsProps } from 'react-google-maps/lib/withScriptjs';
import { Dispatch } from '../@types/Dispatcher';
import { selectWelfareCentre } from '../actions/thunkActions';
import { mapFocused } from '../actions/actions';
import { MobileBreakpoint } from '../constants/config';

export interface IMapContainerProps extends
  WithGoogleMapProps,
  WithScriptjsProps {
}

const mapStateToProps = (state: IStore): IMapStateProps => ({
  isMobileView: state.windowSize.width < MobileBreakpoint,
  welfareCentres: state.filteredWelfareCentres,
});

const mapDispatchToProps = (dispatch: Dispatch): IMapDispatchProps => ({
  onMapFocused: () => dispatch(mapFocused()),
  onWelfareCentreSelected: (id: string) => dispatch(selectWelfareCentre(id)),
});

export const Map: ComponentClass<IMapContainerProps> = withScriptjs(withGoogleMap(connect(
  mapStateToProps,
  mapDispatchToProps,
)(MapComponent)));
