import {
  App as AppComponent,
  IAppDispatchProps
} from '../components/App';
import { Dispatch } from '../@types/Dispatcher';
import { appInit } from '../actions/thunkActions';
import { connect } from 'react-redux';
import { ComponentClass } from 'react';
import { WindowSize } from '../@types/windowSize';
import { windowResized } from '../actions/actions';

export interface IAppContainerProps {
}

const mapDispatchToProps = (dispatch: Dispatch): IAppDispatchProps => ({
  onInit: () => dispatch(appInit()),
  onWindowResized: (size: WindowSize) => dispatch(windowResized(size)),
});

export const App: ComponentClass<IAppContainerProps> = connect(
  null,
  mapDispatchToProps,
)(AppComponent);
