import { initialState } from '../stores/initialState';
import { Action } from '../@types/Dispatcher';
import {
  App_GoBack,
  App_Map_Focused,
  App_Search_Confirmed,
  App_Search_Focused,
  App_WelfareCentre_Selected
} from '../constants/actionTypes';
import { WelfareCentre } from '../models/WelfareCentre';

export function selectedWelfareCentre(
  state = initialState.selectedWelfareCentre,
  action: Action,
): WelfareCentre {
  switch (action.type) {
    case App_WelfareCentre_Selected:
      return action.payload.selectedWelfareCentre;

    case App_GoBack:
    case App_Search_Focused:
    case App_Map_Focused:
      return null;

    default:
      return state;
  }
}
