import { WelfareCentre } from '../models/WelfareCentre';
import { ServiceCategoryMap } from '../models/ServiceCategoryMap';
import Fuse from 'fuse.js';
import { MaxSearchPatternLength } from '../constants/config';

interface ISearchParams {
  readonly categoryKey: string;
  readonly fullText: string;
}

interface IServiceCategory {
  readonly codename: string;
  readonly name: string;
}

export const filterWelfareCentres = (
  welfareCentres: Array<WelfareCentre>,
  searchPhrase: string,
): Array<WelfareCentre> => {
  const searchParams = getSearchParamsFromSearchPhrase(searchPhrase);

  const centresByCategory = searchParams.categoryKey ?
    welfareCentres.filter((centre: WelfareCentre) => containsServiceCategory(centre.serviceCategories.value, searchParams.categoryKey)) :
    welfareCentres;

  if (searchParams.fullText) {
    const fuseOptions: Fuse.FuseOptions<WelfareCentre> = {
      keys: [
        'name.value',
        'city.value',
      ],
      maxPatternLength: MaxSearchPatternLength,
      threshold: 0.2,
    };
    const fuse = new Fuse(centresByCategory, fuseOptions);
    const centresByFullText = fuse.search(searchParams.fullText);

    return centresByFullText;
  }

  return centresByCategory;
};

export const getSearchParamsFromSearchPhrase = (searchPhrase: string): ISearchParams => {
  let categoryKey = null;
  let fullText = searchPhrase;

  ServiceCategoryMap.forEach((value: string, key: string) => {
    if (searchPhrase.startsWith(value)) {
      categoryKey = key;
    }
  });

  if (categoryKey) {
    const categoryValue = ServiceCategoryMap.get(categoryKey);
    fullText = fullText.replace(categoryValue, '');
  }

  return {
    categoryKey,
    fullText,
  };
};

const containsServiceCategory = (serviceCategories: Array<IServiceCategory>, key: string): boolean => {
  return serviceCategories.some((category: IServiceCategory) => category.codename === key);
};

export const getWelfareCentresWithinBounds = (
  welfareCentres: Array<WelfareCentre>,
  bounds: google.maps.LatLngBounds
): Array<WelfareCentre> => {
  return welfareCentres.filter((centre: WelfareCentre) =>
    bounds.contains({lat: centre.gpsLatitude.value, lng: centre.gpsLongitude.value})
  );
};
