import React, { Component } from 'react';
import './SideBar.css';
import { Search } from '../containers/Search';
import { WelfareCentre } from '../models/WelfareCentre';
import { WelfareCentreDetails } from './WelfareCentreDetails';

export interface ISideBarStateProps {
  readonly selectedWelfareCentre: WelfareCentre | null;
}

type SideBarProps = ISideBarStateProps;

export class SideBar extends Component<SideBarProps> {
  static displayName = 'SideBar';

  render(): JSX.Element {
    const {
      selectedWelfareCentre,
    } = this.props;

    return (
      <div className="sidebar">
        <Search />
        {selectedWelfareCentre && (
          <WelfareCentreDetails welfareCentre={selectedWelfareCentre}/>
        )}
      </div>
    );
  }
}
