import {IWelfareCentreRepository} from "./interfaces/IWelfareCentreRepository";
import {WelfareCentre} from "../models/WelfareCentre";
import {deliveryClient} from "../kentico/deliveryClient";
import {ItemResponses} from 'kentico-cloud-delivery';

export const welfareCentreRepository: IWelfareCentreRepository = {
  getAll(): Promise<ItemResponses.DeliveryItemListingResponse<WelfareCentre>> {
    return deliveryClient.items<WelfareCentre>()
      .type('welfare_centre')
      .elementsParameter(['name', 'service_categories', 'city', 'gps_latitude', 'gps_longitude'])
      .getPromise();
  },

  getById(id: string): Promise<ItemResponses.DeliveryItemResponse<WelfareCentre>> {
    return deliveryClient.item<WelfareCentre>(id)
      .getPromise();
  }
}
