import React, {Component} from 'react';
import { ServiceCategoryMap } from '../models/ServiceCategoryMap';
import './CategorySearch.css';
import icons from '../assets/icons.svg';
import { renderSvgIcon } from '../utils/renderUtils';

export interface ICategorySearchCallbackProps {
  readonly onCategorySelected: (key: string) => void;
}

type CategorySearchProps = ICategorySearchCallbackProps;

export class CategorySearch extends Component<CategorySearchProps> {
  static displayName = 'CategorySearch';

  private _renderCategories(): Array<JSX.Element> {
    const categories = Array();
    ServiceCategoryMap.forEach((value, key) =>
      categories.push(
        <li
          className="category-search__category"
          key={key}
          onClick={() => this.props.onCategorySelected(key)}
        >
          <span className="category-search__category-icon">
            {renderSvgIcon(icons + '#' + key)}
          </span>
          {value}
        </li>
      )
    );
    return categories;
  }

  render(): JSX.Element {
    return (
      <ul className="category-search">
        {this._renderCategories()}
      </ul>
    );
  }
}
