import { DeliveryClient, TypeResolver } from "kentico-cloud-delivery";
import { WelfareCentre } from "../models/WelfareCentre";
import {KenticoProjectId} from "../constants/config";

export const deliveryClient = new DeliveryClient({
  projectId: KenticoProjectId,
  typeResolvers: [
    new TypeResolver("welfare_centre", () => new WelfareCentre)
  ]
});
