import React, { Component } from 'react';
import MarkerWithLabel from 'react-google-maps/lib/components/addons/MarkerWithLabel';

export interface ICustomMarkerStateProps {
  readonly id: string;
  readonly latLng: LatLng;
  readonly name: string;
  readonly onClick: (id: string, latLng: google.maps.LatLng) => void;
  readonly showLabels: boolean;
}

type CustomMarkerProps = ICustomMarkerStateProps;

export class CustomMarker extends Component<CustomMarkerProps> {
  static displayName = 'CustomMarker';

  private _onMarkerClick = (event: google.maps.MouseEvent): void => {
    this.props.onClick(this.props.id, event.latLng);
  };

  render(): JSX.Element {
    const {
      id,
      latLng,
      name,
      showLabels,
    } = this.props;

    return (
      <MarkerWithLabel
        labelAnchor={new google.maps.Point(-20, 30)}
        labelClass={'marker-with-label'}
        onClick={this._onMarkerClick}
        position={{ lat: latLng.lat, lng: latLng.lng }}
        title={name}
      >
        <span>
          {showLabels && name}
        </span>
      </MarkerWithLabel>
    )
  }
}
