import { Action, Dispatch, ThunkPromise} from '../../@types/Dispatcher';
import {App_Init_Finished } from '../../constants/actionTypes';
import { WelfareCentre } from '../../models/WelfareCentre';
import { welfareCentreRepository } from '../../repositories/welfareCentreRepository';

interface IAppInitFinishedPayload {
  readonly loadedWelfareCentres: Array<WelfareCentre>;
}

const appInitFinished = (payload: IAppInitFinishedPayload): Action => ({
  type: App_Init_Finished,
  payload,
});

export const createAppInitAction = () =>
  (): ThunkPromise =>
    async (dispatch: Dispatch) => {
      const welfareCentres = await welfareCentreRepository.getAll();
      dispatch(appInitFinished({
        loadedWelfareCentres: welfareCentres.items,
      }));
    };
