import { WelfareCentre } from '../models/WelfareCentre';
import { Action } from '../@types/Dispatcher';
import { initialState } from '../stores/initialState';
import { App_Search_Confirmed } from '../constants/actionTypes';

export function filteredWelfareCentres(
  state = initialState.filteredWelfareCentres,
  action: Action,
): Array<WelfareCentre> {
  switch (action.type) {
    case App_Search_Confirmed:
      return action.payload.filteredWelfareCentres;
    default:
      return state;
  }
}
