import React, { Component } from 'react';
import { WelfareCentre } from '../models/WelfareCentre';
import { GoogleStreetViewMetadataUrl, GoogleStreetViewUrl } from '../constants/mapConfig';
import './WelfareCentreDetails.css';
import { isEmptyOrWhitespace, stripHtml } from '../utils/general/textUtils';
import { ensureUrlHttpProtocol } from '../utils/general/urlUtils';
import { ajax, AjaxResponse } from 'rxjs/ajax';
import streetViewNotFound from '../assets/streetview-not-found.png';

export interface IWelfareCentreDetailsStateProps {
  readonly welfareCentre: WelfareCentre;
}

export interface IWelfareCentreDetailsState {
  readonly streetViewImageUrl?: string;
}

type WelfareCentreDetailsProps = IWelfareCentreDetailsStateProps;

export class WelfareCentreDetails extends Component<WelfareCentreDetailsProps, IWelfareCentreDetailsState> {
  static displayName = 'WelfareCentreDetails';

  constructor(props: WelfareCentreDetailsProps) {
    super(props);
    this.state = {};
  }

  private _tryLoadStreetViewImage = (): void => {
    const {
      welfareCentre,
    } = this.props;

    const street = welfareCentre.street.value;
    const city = welfareCentre.city.value;

    const encodedLocation = encodeURI(`${street}, ${city}`);
    const streetViewMetadataUrl = `${GoogleStreetViewMetadataUrl}&location=${encodedLocation}&size=800x400`;
    const streetViewImageUrl = `${GoogleStreetViewUrl}&location=${encodedLocation}&size=800x400`;

    ajax.get(streetViewMetadataUrl)
      .subscribe((response: AjaxResponse) => {
        this.setState(() => ({
          streetViewImageUrl: response.response.status !== 'ZERO_RESULTS' ? streetViewImageUrl : undefined,
        }));
      });
  };

  componentDidMount(): void {
    this._tryLoadStreetViewImage();
  }

  componentDidUpdate(prevProps: WelfareCentreDetailsProps): void {
    if (prevProps.welfareCentre !== this.props.welfareCentre) {
      this._tryLoadStreetViewImage();
    }
  }

  render(): JSX.Element {
    const {
      welfareCentre,
    } = this.props;
    const {
      streetViewImageUrl,
    } = this.state;

    const name = welfareCentre.name.value;
    const street = welfareCentre.street.value;
    const city = welfareCentre.city.value;
    const zip = welfareCentre.zip.value;
    const openingHours = welfareCentre.openingHours.value;
    const description = welfareCentre.serviceDescription.value;
    const phone = welfareCentre.phone.value;
    const web = welfareCentre.web.value;

    return (
      <div className="welfare-details">
        <div className="welfare-details__image">
          <img src={streetViewImageUrl || streetViewNotFound} alt={street} />
        </div>
        <h2 className="welfare-details__title">{name}</h2>

        {!isEmptyOrWhitespace(stripHtml(description)) && (
          <div
            className="welfare-details__block welfare-details__block--description"
          >
            <div dangerouslySetInnerHTML={{__html: welfareCentre.serviceDescription.getHtml()}} />
          </div>
        )}

        <div className="welfare-details__block welfare-details__block--address">
          <p>{street}</p>
          <p>{zip} {city}</p>
        </div>

        {!isEmptyOrWhitespace(stripHtml(openingHours)) && (
          <div
            className="welfare-details__block welfare-details__block--opening-hours"
          >
            <p className="welfare-details__block-title">Otevírací doba</p>
            <div dangerouslySetInnerHTML={{__html: welfareCentre.openingHours.getHtml()}} />
          </div>
        )}

        <div className="welfare-details__block welfare-details__block--contacts">
          <p>{!isEmptyOrWhitespace(phone) && `Tel: ${phone}`}</p>
          <p>
            {!isEmptyOrWhitespace(web) && (
              <a href={ensureUrlHttpProtocol(web)} target="_blank">{web}</a>
            )}
          </p>
        </div>
      </div>
    );
  }
}
