import { WelfareCentre } from '../../models/WelfareCentre';
import { Action, Dispatch, GetState, ThunkPromise } from '../../@types/Dispatcher';
import { App_WelfareCentre_Selected } from '../../constants/actionTypes';
import { IStore } from '../../stores/IStore';
import { welfareCentreRepository } from '../../repositories/welfareCentreRepository';

interface IWelfareCentreSelectedPayload {
  readonly selectedWelfareCentre: WelfareCentre;
}

const welfareCentreSelected = (payload: IWelfareCentreSelectedPayload): Action => ({
  type: App_WelfareCentre_Selected,
  payload,
});

export const createSelectWelfareCentreAction = () =>
  (id: string): ThunkPromise =>
    async (dispatch: Dispatch, getState: GetState<IStore>) => {
      const {
        selectedWelfareCentre,
      } = getState();

      const selectionChanged = !selectedWelfareCentre || selectedWelfareCentre.system.codename != id;
      if (selectionChanged) {
        const welfareCentre = await welfareCentreRepository.getById(id);
        dispatch(welfareCentreSelected({selectedWelfareCentre: welfareCentre.item}));
      }
    };
