import { CancelledDelay } from './generalUtilities';
import RSVP from 'rsvp';

export function bootstrapPromises(): void {
// Handles uncaught error within promises
  (window as any).Promise = RSVP.Promise;
  RSVP.on('error', (reason) => {
    if (reason) {
      if (!(reason instanceof CancelledDelay)) {
        console.error(reason, reason.stack);
      }
    }
    else {
      console.error('A reasonless uncaught promise error.');
    }
  });
}
