import {
  App_GoBack,
  App_Map_Focused,
  App_Search_Confirmed,
  App_Search_Focused
} from '../constants/actionTypes';
import { Action } from '../@types/Dispatcher';

export function isSearchConfirmed(
  state = false,
  action: Action,
): boolean {
  switch (action.type) {
    case App_Search_Confirmed:
    case App_Map_Focused:
      return true;
    case App_Search_Focused:
      return false;
    default:
      return state;
  }
}
