import { App_Search_Confirmed } from '../../constants/actionTypes';
import { Action, Dispatch, ThunkPromise, GetState } from '../../@types/Dispatcher';
import { IStore } from '../../stores/IStore';
import { WelfareCentre } from '../../models/WelfareCentre';
import { filterWelfareCentres } from '../../utils/searchUtils';

interface ISearchConfirmedPayload {
  readonly filteredWelfareCentres: Array<WelfareCentre>;
}

const searchConfirmed = (payload: ISearchConfirmedPayload): Action => ({
  type: App_Search_Confirmed,
  payload,
});

export const createSearchConfirmedAction = () =>
  (searchPhrase: string): ThunkPromise =>
    async (dispatch: Dispatch, getState: GetState<IStore>) => {
      const {
        loadedWelfareCentres,
      } = getState();

      const filteredWelfareCentres = filterWelfareCentres(loadedWelfareCentres, searchPhrase);
      dispatch(searchConfirmed({ filteredWelfareCentres }));
    };
