import { Action } from '../@types/Dispatcher';
import { App_GoBack, App_Map_Focused, App_Search_Focused, App_Window_Resized } from '../constants/actionTypes';
import { WindowSize } from '../@types/windowSize';

export const mapFocused = (): Action => ({
  type: App_Map_Focused,
});

export const searchFocused = (): Action => ({
  type: App_Search_Focused,
});

export const goBack = (): Action => ({
  type: App_GoBack,
});

export const windowResized = (size: WindowSize): Action => ({
  type: App_Window_Resized,
  payload: size,
});
